import * as React from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import { TabContext, TabList, TabPanel } from '@material-ui/lab';
import florest from '../assets/florest.png';
import { Image } from 'mui-image'
import Button from '@mui/material/Button';
import { Typography } from '@mui/material';
import Grid from '@mui/material/Grid';
import { useNavigate } from 'react-router-dom';

export default function LabTabs() {
  
  const [value, setValue] = React.useState('1');

  const navigate = useNavigate()

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box 
        display="flex"
        flexDirection="column"
        sx={{
            height:"100vh",
            width: '100%',
            typography: 'body1',
            backgroundImage : `url(${florest})`,
            backgroundSize: "100% 100%",
            color: "white"
        }}>
      
      <TabContext value={value}>
        <Box 
            sx={{ 
                width: '100%',
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'flex-end',
                justifyContent: 'space-between',
                background: "linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2))",
                height: '15%',
                borderBottom: 1,
                borderColor: 'divider'
            }}>
            <Box sx={{ ml: 3, height: '60%'}}><Image alt="logo" src='https://i.ibb.co/XjS840S/logo.png' /></Box>

            <TabList onChange={handleChange} aria-label="lab API tabs example" textColor="white" indicatorColor="secondary">
                <Tab label="Home" value="1" />
                <Tab label="Quem somos" value="2" />
                <Tab label="Porque alugar" value="3" />
            </TabList>

            <Box sx={{ mr: 3, width: '17vw', display: 'flex', flexDirection: 'row', justifyContent: 'space-between'}}>
                <Button onClick={() => { navigate('/login') }} variant="contained" style={{ color: "black", backgroundColor: "#EDD929", borderRadius: 50 }}>Login</Button>
                <Button onClick={() => { navigate('/sign') }} variant="contained"  style={{ color: "black", backgroundColor: "#EDD929", borderRadius: 50 }}>Cadastre-se</Button>
            </Box>
        </Box>

        <Box> 
            <TabPanel value="1">
                <Box sx={{ width: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
                     <Typography variant="h1" component="h2"> Alugue essa ideia</Typography>
                     <Typography mt={2} paragraph={true} sx={{fontSize: 20}}>
                        Todos ns somos inquilinos neste planeta,
                        por isso seja um morador consciente e acrescente mais vida a nosso planeta!
                     </Typography>
                </Box>
            </TabPanel>
            
            <TabPanel value="2">
                <Box sx={{width: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
                    <Typography variant="h2" component="h2" >Sobre ns</Typography>
                    <Box sx={{width: '100%', display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly'}}>
                        <Typography mt={2} paragraph={true} sx={{fontSize: 20, width: '35%'}}>
                            Conectamos proprietrios que desejam alugar seu terreno
                            com empresas que ajudam a construir um mundo mais sustentvel.
                        </Typography>

                        <Typography mt={2} paragraph={true} sx={{fontSize: 20, width: '35%'}}>
                            Nossa misso  unir pessoas e empresas com o mesmo propsito,
                            reduzir o impacto ambiental atravs do reflorestamento de terrenos.
                        </Typography>
                    </Box>
                </Box>
            </TabPanel>
            
            <TabPanel value="3">
                <Box sx={{width: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
                    <Typography variant="h2" component="h2"> Por que alugar?</Typography>
                    <Grid container mt={2} spacing={6} sx={{flexGrow:1, justifyContent: 'space-between'}}>
                        <Grid item xs={6}>
                            <Typography mt={2} paragraph={true} sx={{fontSize: 20}}>
                                As rvores plantadas oferecem muitos benefcios no clima,
                                na biodiversidade, sade e emprego. Uma rvore armazena cerca de 150 Kg de CO2 por ano.
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography mt={2} paragraph={true} sx={{fontSize: 20 }}>
                                As rvores  podem resfriar o ar em at 8C, reduzindo a necessidade de ar condicionado em 30%.
                                As rvores so excelentes filtros de ar, removendo poluentes nocivos.
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography mt={2} paragraph={true} sx={{fontSize: 20 }}>
                                Em mdia, 40% das chuvas sobre a terra se originam da evapotranspirao das plantas.
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography mt={2} paragraph={true} sx={{fontSize: 20}}>
                                Mais de 70% das chuvas na bacia do Rio da Prata se originam da evatranspirao da floresta amaznica. 
                            </Typography>
                        </Grid>

                    </Grid>
                </Box>
            </TabPanel>
        </Box>
      </TabContext>
    </Box>
  );
}
